const button = document.getElementById('songListToggle');

if (button) {
  const songList = document.getElementById('songList');

  let hidden = false;
  const hide = () => {
    songList.style.display = 'none';
    hidden = true;
  };
  const show = () => {
    songList.style.display = '';
    hidden = false;
  };
  const toggle = () => hidden ? show() : hide();

  const media = window.matchMedia('(min-width: 900px)');
  media.addListener(run);
  run(media);

  function run (media) {
    if (!media.matches) {
      hide();
      button.addEventListener('click', toggle);
      songList.addEventListener('click', hide);
    } else {
      show();
      button.removeEventListener('click', toggle);
      songList.removeEventListener('click', hide);
    }
  }
}
